import { CLASS_NAMES, TYPES } from "../constants"
import { HighlightItem } from "./generate-rpjson-stylesheet"

type Props = {
  id: string
}

export const highlightEntity = (style: object = {}, { id }: Props): HighlightItem => {
  const type = id.indexOf(",") !== -1 ? TYPES.EVENT : TYPES.ENTITY
  const mapped = id
    .split(",")
    .filter(item => item)
    .map(subItem => `.${CLASS_NAMES.ID_PREFIX}${subItem}`)
    .join("")
  return {
    style,
    className: `& .${CLASS_NAMES.TYPE_PREFIX}${type}${mapped}`,
  }
}
